@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;700&family=Playfair+Display:ital,wght@0,400;0,700;1,400&family=Roboto:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-family: 'Playfair Display';
  color: #56001e;
  letter-spacing: 0.9vw;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.headline {
  font-family: 'Playfair Display', serif;
  white-space: nowrap;
  font-size: 3vw;
}

.App-header {
  background-color: #F3F0DC;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #56001e;
  /* border-bottom: 3px solid #56001e; */
}

.App-link {
  color: #61dafb;
}

hr.animated {
  width: 100%;
  color: #56001e;
  animation: expand 2s ease-in-out;
  border-top: solid 5px #56001e;
  margin: 0;
  box-sizing: border-box;
}
.quote {
  font-family:  Calibri, 'Gill Sans', 'Gill Sans MT', 'Trebuchet MS', sans-serif;
  font-style: italic;
  font-size: 2em;
  font-weight: 200;
  padding: 17vh 0;
  color: #56001e;
}

@keyframes expand {
  from {
    transform: scaleX(0);
  }
  to {
    transform: scaleX(1);
  }
}

@media only screen and (max-width: 740px) {
  .App-header {
    flex-direction: column;
  }
}
